import _ from 'lodash'

export default {
	created() {
		// 在500ms内只执行一次
		this.debouncedrefreshHandle = _.debounce(this.refreshHandle,500)
	},
	data() {
		return {
			limit: 8,
			skip: 0,
			current: 0,
			banners: [{
				id: 1,
				url: '/static/images/banner.jpg'
			}, {
				id: 2,
				url: '/static/images/banner.jpg'
			}, {
				id: 3,
				url: '/static/images/banner.jpg'
			}],
			circular: true, // 无缝滚动
			autoplay: true,
			imageHeights: [],
			current: 0,
			likesScaleHeight: 0,
			scaleHeight: 0,
			route: '',
			showToTop: false,
			scrollTop: 0,
			old: {
				scrollTop: 0
			}
		}
	},
	computed: {
		books: function() {
			return this.$store.state.books
		},
		likeBooks: function() {
			return this.$store.state.likeBooks
		},
		newBooks: function() {
			return this.$store.state.newBooks
		},
		isOver: function () {
			return this.$store.state.isOver
		}
	},
	methods: {
		lower: function(e) {
			console.log('low')
		},
		goReading(_id) {
			uni.navigateTo({
				url: '/pages/read/read?_id=' + _id
			})
		},
		// 换一换
		async refreshHandle() {
			this.skip += 8;
			await this.getBookList();
			// 数据库的书已经光了...
			if(this.isOver){
				this.skip = 0
				await this.getBookList();
				this.$store.commit('SETISOVER',false)
			}
		},
		async getBookList() {
			uni.showLoading({
				title: "加载中..."
			})
			// 读取数据库中的书籍
			await this.$store.dispatch('SETBOOKS', {
				skip: this.skip,
				limit: this.limit
			})
			uni.hideLoading()
		},
		changeHandle(e) {
			this.current = e.detail.current;
		},
		async loadHandler(e) {
			const _this = this
			const query = uni.createSelectorQuery()
			await query.select('.swiper-item').boundingClientRect(data => {
				// 缩放后的高度
				if (data) {
					_this.scaleHeight = data.height + 20
				}
			}).exec()
		},
		async loadLikesHandler(e) {
			const _this = this
			const query = uni.createSelectorQuery()
			await query.select('.hot .swiper-item').boundingClientRect(data => {
				// 缩放后的高度
				if (data) {
					_this.likesScaleHeight = data.height
				}
			}).exec()
		},
		goTopHandle() {
			// 回到顶部
			this.scrollTop = this.old.scrollTop
			this.$nextTick(() => {
				this.scrollTop = 0
			})
		},
		scrollHandle(event) {
			this.old.scrollTop = event.detail.scrollTop
			if (event.detail.scrollTop > 666) {
				this.showToTop = true
			} else {
				this.showToTop = false
			}
		},
		// toRanking() {
		// 	uni.navigateTo({
		// 		url: "/pages/ranking/ranking"
		// 	})
		// },
		toBoys() {
			uni.navigateTo({
				url: "/pages/boys/boys"
			})
		},
		toGirls() {
			uni.navigateTo({
				url: "/pages/girls/girls"
			})
		},
		toBookStall() {
			uni.navigateTo({
				url: "/pages/bookstall/bookstall"
			})
		}
	},
	onShow() {},
	onLoad: function() {
		const pages = getCurrentPages()
		const ins = pages[pages.length - 1]
		const route = ins.route
		this.route = route
	}
}
